  font-size: 11px;
}

.menu > ul {
  margin: 0;
  width: 100%;
  list-style: none;
}

.menu > ul:before,
.menu > ul:after {
  content: '';
  display: table;
}

.menu > ul:after {
  clear: both;
}

.menu > ul > li {
  float: left;
  padding: 0;
  margin: 0;
}

.menu > ul > li a {
  padding: 15px 15px;
  display: block;
  color: #333;
